import { Role } from "constants/role.enum";

export const getRoleName = (role: Role) => {
    switch (role) {
        case Role.ADMIN:
            return "";
        case Role.ENROLLEE:
            return "";
        case Role.STUDENT:
            return "";
        case Role.SELLECTION_COMMITE:
            return "  ";
        case Role.SUPERVISOR:
            return "";
        case Role.TEACHER:
            return "";
        case Role.MANAGER:
            return "";
    }
};
